body {
  font-family: 'Helvetica Neue', Helvetica, arial, sans-serif;
  margin: 0; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: top; }

.viewport-slider-item {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row-reverse;
      flex-direction: row-reverse;
  -ms-flex-pack: center;
      justify-content: center; }
  .viewport-slider-item .viewport-slider-button {
    border: 1px solid #fff;
    color: #fff;
    cursor: pointer;
    opacity: .7;
    padding: 12px;
    text-align: center;
    text-decoration: none;
