/* ARTWORK */
.Artwork {
  -ms-flex-align: center;
      align-items: center;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(20%, white), color-stop(70%, grey));
  background: -webkit-linear-gradient(white 20%, grey 70%);
  background: -o-linear-gradient(white 20%, grey 70%);
  background: linear-gradient(white 20%, grey 70%);
  width: 100%;
  position: inherit;
  min-height: 860px; }

.art-images {
  position: -webkit-sticky;
